<template>
	<view class="box">
		<view class="mask"></view>
		<view class="cart-box">
			<view class="cart-img">
				<image lazy-load class="img scale11" :src="getPrice.mainPicUrl" mode="aspectFill"></image>
				<view class="money-box">
					<view class="money">
						<text v-if="goodDetail.orderType!=='COUPON'&&goodDetail.orderType!=='ACTIVE_POINT'">￥</text>
						<template v-if="goodDetail.orderType=='COUPON'">
							{{getPrice.couponValue | formatNum}}
						</template>
						<!--活动积分 -->
						<template v-else-if="goodDetail.orderType=='ACTIVE_POINT'">
							{{getPrice.jfValue | formatNum}}
						</template>
						<!--其他 -->
						<template v-else>
							{{getPrice.promptPrice | formatNum}}
						</template>

						<text v-if="goodDetail.orderType=='COUPON'" class="font-small">团批劵</text>
						<text v-if="goodDetail.orderType=='ACTIVE_POINT'" class="font-small">积分</text>
					</view>
					<view class="choose-text" v-if="specList.length>0">
						已选：<text class="type">{{all_spec}}</text>
					</view>
				</view>
				<view class="iconfont iconcuowu" @tap="cartClose"></view>
			</view>
			<view class="type-cont">
				<template v-for="(item,k) in specList">
					<view class="type-box" :key="k">
						<view class="title">
							{{item.spuSpecName}}
						</view>
						<view class="btn-box">
							<template v-for="(val,i) in item.o2oSkuSpecList">
								<view :key="i" class="gray-btn" :class="{'active':i==item.spec_choose}" @tap.stop='chooseSpec(k,i)'>{{val.skuSpecValue}}</view>
							</template>
						</view>
					</view>
				</template>
				<view class="buy-num">
					<view class="title">购买数量</view>
					<add-reduce :num="buy_num" @getNum='getBuyNum'></add-reduce>
				</view>
			</view>
			<view class="sure-btn" @tap="sure" :class="{'padd-safe':shapedScreen}">
				确认
			</view>
		</view>

	</view>
</template>

<script>
	import addReduce from './add-reduce.vue'
	export default {
		data() {
			return {
				all_spec: "", //string 选中的规格的组合 
				buy_num: 1,
				is_loading: false,
				shapedScreen: this.shapedScreen
			}
		},
		components: {
			addReduce
		},
		props: {
			specList: {
				type: Array,
				default: function() {
					return [{}]
				},
			},
			spuSpecSkuMap: {
				default: [],
				type: Array
			},
			goodDetail: {
				type: Object,
				default: {}
			},
			showType: {
				type: String,
				default: 'shopcar'
			}
		},
		created() {
			this.goodType = this.goodDetail.orderType
		},
		methods: {
			chooseSpec(k, i) {
				this.$emit('specChoose', {
					key: k,
					index: i
				})
			},
			cartClose() {
				this.$emit('cartClose', true)
			},
			getBuyNum(e) {
				this.buy_num = e
			},
			sure() {
				if (this.showType == 'shopcar') {
					//加入购物车
					this.joinCar()
				} else {
					//下订单
					this.comfirmOrder()
				}
			},
			// 加入购物车
			joinCar() {
				if (this.is_loading) return;
				this.is_loading = true;
				this.$api.post('/o2oShopingCart/insert2Cart', {
					customerName: this.$common.getLocalSync('customerName'),
					customerSeqId: this.$common.getLocalSync('customerSeqId'),
					effectivePrice: this.getPrice.promptPrice,
					merchantSeqId: this.goodDetail.merchantSeqId,
					merchantShopId: this.goodDetail.merchantShopId,
					merchantShopName: this.goodDetail.merchantShopName,
					qty: this.buy_num,
					seckillSeqId: "",
					skuSeqId: this.getPrice.skuSeqId,
					skuSpecs: this.all_spec,
					skuTitle: this.goodDetail.spuTitle,
					skuUrl: this.goodDetail.mainPicUrl,
					shareCode: this.goodDetail.shareCode
				}).then(res => {
					this.is_loading = false;
					if (res.code == 200) {
						this.$box.toast('成功加入购物车');
						this.$emit('cartClose', true) //关闭购物车弹框
						this.$emit('cartReload', true) //更新购物车数量
					} else if (res.code !== 401) {
						this.$box.confirm(res.msg, false)
					}
				}).catch(e => {
					this.is_loading = false;
				})
			},
			//下订单
			comfirmOrder() {
				if (this.is_loading) return;
				this.is_loading = true;
				let oneShopingShopObj = {
					"customerName": this.$common.getLocalSync('customerName'),
					"customerSeqId": this.$common.getLocalSync('customerSeqId'),
					"merchantShopId": this.goodDetail.merchantShopId,
					"promptPrice": this.getPrice.promptPrice,
					"couponValue": this.getPrice.couponValue,
					"jfValue": this.getPrice.jfValue,
					"qty": this.buy_num,
					"seckillSeqId": "",
					"skuSeqId": this.getPrice.skuSeqId,
					"skuSpecs": this.all_spec,
					'orderType': this.goodDetail.orderType,
					'shareCode': this.goodDetail.shareCode
				}
				this.$api.post('/o2oShopingCart/add2OrderDirectly', oneShopingShopObj).then(res => {
					this.is_loading = false
					if (res.code == 200) {
						this.$store.commit('saveOrderConfirm', res.data)
						uni.navigateTo({
							url: '/pages/goods/order_confirm?type=' + this.goodType
						})
					} else if (res.code !== 401) {
						this.$box.confirm(res.msg, false)
					}
				}).catch(e => {
					this.is_loading = false;
				})
			}
		},
		computed: {
			getPrice() {
				try {
					if (this.specList.length > 0) {
						// 多规格
						let choose_text = "";
						let choose_id = "";
						let choose_sku = [];
						for (let i in this.specList) {
							let the_speclist = this.specList[i]
							let spec_choose = the_speclist.spec_choose
							choose_text += the_speclist.o2oSkuSpecList[spec_choose].skuSpecValue + ','
						}
						this.all_spec = choose_text.slice(0, -1);
						choose_sku = this.spuSpecSkuMap.filter(item => item.unionKeyName == this.all_spec);
						return choose_sku[0]
					} else {
						// 没有规格 就直接取o2oSpuSpecSkuMap
						return this.spuSpecSkuMap[0]
					}

				} catch (err) {
					console.log(err);
				}

			}
		}
	}
</script>

<style lang="scss">
	.cart-box {
		width: 100%;
		height: 60vh;
		max-height: 100%;
		background: white;
		position: fixed;
		bottom: 0;
		left: 0;
		border-radius: 16rpx 16rpx 0 0;
		z-index: 100;
		padding: 34rpx 0;
		padding-bottom: 110rpx;

		.cart-img {
			display: flex;
			align-items: center;
			position: relative;
			padding-bottom: 20rpx;
			height: 200rpx;
			padding: 0 34rpx;
			.iconcuowu {
				position: absolute;
				top: 0rpx;
				right: 0rpx;
				font-size: 40rpx;
				padding: 20rpx;
				z-index: 1;
			}

			.img {
				width: 200rpx;
				height: 200rpx;
				border-radius: 7rpx;
				background: #eee;
				flex-shrink: 0;
				margin-right: 30rpx;
			}

			.money-box {
				flex-grow: 1;

				.money {
					width: 100%;
					font-size: 44rpx;
					font-weight: bold;
					margin: 20rpx 0;
					color: $color-primary;
				}

				.choose-text {
					font-size: 26rpx;
					margin: 40rpx 0;

					.type {
						margin-left: 10rpx;

					}
				}
			}
		}
	}

	.type-cont {
		height: calc(100% - 200rpx);
		overflow-y: auto;
		padding: 0 34rpx;
	}

	.type-box {
		width: 100%;
		margin: 20rpx 0;

		.title {
			font-size: 26rpx;
			color: rgba(153, 153, 153, 1);
			line-height: 42rpx;
		}

		.btn-box {
			display: flex;
			flex-wrap: wrap;

			.gray-btn {
				background: #EEEEEE;
				color: #999999;
				font-size: 26rpx;
				padding: 12rpx 24rpx;
				border-radius: 30rpx;
				margin: 18rpx 34rpx;
				margin-left: 0;
				cursor: pointer;
			}

			.active {
				border: 1px solid $color-primary;
				background: $color-light-primary;
				color: $color-primary;
			}
		}
	}

	.buy-num {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-top: 1px solid #EEEEEE;
		padding: 20rpx 0;

		.title {
			font-size: 26rpx;
			color: #999;
		}

		.num-box {
			width: 150rpx;
			justify-content: space-between;
			margin: 40rpx 0;
			align-items: center;

			.num {
				float: left;
				font-size: 26rpx;
				padding: 0 10rpx;
				min-width: 50rpx;
				text-align: center;
			}

			.iconfont {
				width: 38rpx;
				height: 38rpx;
				line-height: 38rpx;
				background: #EEEEEE;
				border-radius: 100%;
				float: left;
				color: #333;
				font-size: 20rpx;
				text-align: center;
				cursor: pointer;

			}
		}
	}

	.sure-btn {

		position: absolute;
		left: 0;
		bottom: 0;
		z-index: 1;
		width: 100%;
		height: 110rpx;
		line-height: 110rpx;
		text-align: center;
		font-size: 40rpx;
		color: rgba(255, 255, 255, 1);
		background: $color-primary;
	}
</style>
